@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;600&display=swap);
*{
  margin: 0;
  padding: 0;
}

body {
  font-family: 'Nunito', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.app {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px 24px;
}

.app__searchForm {
  margin-top: 18px;
}

.app__input {
  padding: 6px 16px;
  min-width: 200px;
  max-width: 100vw;
  font-size: 15px;
}

.app__submit {
  padding: 6px 16px;
  font-size: 15px;
  margin-left: 6px;
  background-color: #02aff0;
  border: 2px solid #02aff0;
  border-radius: 4px;
  color: white;
  cursor: pointer;
}

.app__recipes_count {
  margin-top: 10px;
  text-align: left;
  min-width: 200px;
}

.app__recipes {
  margin-top: 24px;
  display: grid;
  grid-template-columns: 250px 250px;
  align-items: baseline;
}

.app__healthLabels {
  padding: 7px 8px;
  margin-left: 6px;
  border-radius: 4px;
}
.recipeTile {
    cursor: pointer;
}
.recipeTile__img{
    height: 200px;
    width: 200px;
    object-fit: cover;
    border-radius: 4px;
    transition: all 300ms;
}

.recipeTile__img:hover {
    transform: scale(1.1);
}

.recipeTile__name {
    max-width: 200px;
    text-align: center;
    margin: 12px 0px;
}
